<template>
  <div class="OneNote" :style="{ backgroundColor : note.color.val }">
    <div class="OneNote-Actions">
      <span class="OneNote-Button icon icon-edit" @click="edit(note._id)"/>
      <span class="OneNote-Button icon icon-trash" @click="del(note._id)"/>
    </div>
    <p class="OneNote-Header">{{ note.name.val }}</p>
    <pre class="OneNote-Body">
      {{ note.body.val }}
    </pre>
  </div>
</template>

<script>
export default {
  name: 'one-note',
  props: ['note'],
  setup (props, { emit }) {
    const edit = (id) => {
      emit('EditNote', id)
    }
    const del = (id) => {
      emit('DelNote', id)
    }
    return {
      edit,
      del
    }
  }
}
</script>

<style lang="stylus">
.OneNote
  margin 5px
  padding 5px
  border-radius 5px
  min-width 200px
  height auto
  background-color var(--white-default)
  box-shadow 0 0 4px var(--blak-transparent)
  &-Actions
    display flex
    justify-content flex-end
    border-bottom 1px solid var(--black-light)
    padding-bottom 4px
  &-Header
    font-style italic
    margin-bottom 5px
    font-size 14px
  &-Button
    font-size 20px
    cursor pointer
    &:not(:last-child)
      margin-right 5px
</style>
